"use client";

import { useState, type ElementType, useCallback, useEffect, useRef } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { Beaker, CheckCircle, Landmark, Lock, Sigma } from 'lucide-react';
import { useUnlockStore, type Subject } from '@/hooks/use-unlock-store';
import { Card, CardHeader, CardTitle } from '@/components/ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Skeleton } from './ui/skeleton';
import { Loader2 } from 'lucide-react';
import { cn } from '@/lib/utils';
import Confetti from 'react-confetti';


type SubjectInfo = {
  id: Subject;
  name: string;
  icon: ElementType;
  color: string;
  glowColor: string;
};

const subjects: SubjectInfo[] = [
  { id: 'science', name: 'Science', icon: Beaker, color: 'hover:border-emerald-500/50', glowColor: 'shadow-emerald-500/50 border-emerald-500/80' },
  { id: 'math', name: 'Math', icon: Sigma, color: 'hover:border-sky-500/50', glowColor: 'shadow-sky-500/50 border-sky-500/80' },
  { id: 'social-studies', name: 'Social Studies', icon: Landmark, color: 'hover:border-amber-500/50', glowColor: 'shadow-amber-500/50 border-amber-500/80' },
];

function UnlockDialog({ subject, isOpen, onOpenChange, onUnlockSuccess }: { subject: SubjectInfo, isOpen: boolean, onOpenChange: (open: boolean) => void, onUnlockSuccess: (subjectId: Subject) => void }) {
  const [code, setCode] = useState('');
  const [error, setError] = useState('');
  const [isUnlocking, setIsUnlocking] = useState(false);
  const { unlockSubject } = useUnlockStore();
  const { toast } = useToast();

  const handleUnlock = async () => {
    setError('');
    setIsUnlocking(true);
    const { success } = await unlockSubject(subject.id, code);
    setIsUnlocking(false);

    if (success) {
      onUnlockSuccess(subject.id);
      onOpenChange(false);
      setCode('');
      toast({
        title: "Success!",
        description: `You've unlocked the content.`,
      });
    } else {
      setError('Invalid or already used code. Please try again.');
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent className="glass-card">
        <DialogHeader>
          <DialogTitle>Unlock {subject.name}</DialogTitle>
          <DialogDescription>Enter your one-time code to view the study materials for this subject.</DialogDescription>
        </DialogHeader>
        <div className="space-y-2">
          <Input 
            placeholder="Enter unlock code"
            value={code}
            onChange={(e) => setCode(e.target.value)} 
            onKeyDown={(e) => e.key === 'Enter' && !isUnlocking && handleUnlock()}
            disabled={isUnlocking}
          />
          {error && <p className="text-sm text-destructive">{error}</p>}
        </div>
        <DialogFooter>
          <Button onClick={handleUnlock} disabled={isUnlocking}>
            {isUnlocking && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Unlock
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}

function SubjectCard({ subject, isUnlocked, onUnlockSuccess }: { subject: SubjectInfo, isUnlocked: boolean, onUnlockSuccess: (subjectId: Subject) => void }) {
  const [isDialogOpen, setDialogOpen] = useState(false);
  const cardRef = useRef<HTMLDivElement>(null);

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    if (!cardRef.current) return;
    const { left, top, width, height } = cardRef.current.getBoundingClientRect();
    const x = (e.clientX - left - width / 2) / 25;
    const y = (e.clientY - top - height / 2) / 25;
    cardRef.current.style.transform = `rotateY(${x}deg) rotateX(${-y}deg)`;

    const bgX = ((e.clientX - left) / width) * 100;
    const bgY = ((e.clientY - top) / height) * 100;
    cardRef.current.style.setProperty('--mouse-x', `${bgX}%`);
    cardRef.current.style.setProperty('--mouse-y', `${bgY}%`);
  };

  const handleMouseLeave = () => {
    if (!cardRef.current) return;
    cardRef.current.style.transform = 'rotateY(0deg) rotateX(0deg)';
  };
  
  const CardContentWrapper = ({ children }: { children: React.ReactNode }) => {
    const commonProps = {
      className: cn(
        "relative w-full h-full p-0 transition-all duration-300 ease-out group glass-card shadow-lg hover:shadow-2xl",
        "transform-style-3d", // Add this for 3D transforms
        !isUnlocked && subject.color,
        isUnlocked && subject.glowColor,
        isUnlocked && "shadow-xl"
      ),
      onMouseMove: handleMouseMove,
      onMouseLeave: handleMouseLeave,
      ref: cardRef
    };

    if (isUnlocked) {
      return (
        <Link href={`/subject/${subject.id}`} className="block w-full h-full [perspective:1000px]">
            <Card {...commonProps}>{children}</Card>
        </Link>
      );
    }
    return (
      <div className="w-full h-full [perspective:1000px]" onClick={() => setDialogOpen(true)} role="button" tabIndex={0}>
        <Card {...commonProps}>
          {children}
        </Card>
      </div>
    );
  };
  
  return (
    <>
      <CardContentWrapper>
        <div className="absolute inset-0 card-highlight-bg opacity-0 group-hover:opacity-100 transition-opacity duration-300 rounded-lg"></div>
        <div className="flex flex-col items-center justify-center text-center h-full p-6 transform-style-3d [transform:translateZ(20px)]">
          <div className="relative mb-4">
            <subject.icon className="h-16 w-16 text-primary transition-transform duration-300 group-hover:scale-125 group-hover:[filter:drop-shadow(0_0_8px_hsl(var(--primary)))]" />
            <div className="absolute -top-1 -right-1 bg-background rounded-full p-1 shadow-md">
              {isUnlocked ? <CheckCircle className="h-6 w-6 text-emerald-500" /> : <Lock className="h-6 w-6 text-muted-foreground" />}
            </div>
          </div>
          <CardHeader className="p-0">
            <CardTitle className="font-headline text-2xl font-bold">{subject.name}</CardTitle>
          </CardHeader>
          <p className="text-muted-foreground mt-2 text-sm">{isUnlocked ? 'Content Unlocked' : 'Locked'}</p>
        </div>
      </CardContentWrapper>
      {!isUnlocked && <UnlockDialog subject={subject} isOpen={isDialogOpen} onOpenChange={setDialogOpen} onUnlockSuccess={onUnlockSuccess} />}
    </>
  );
}

function CardSkeleton() {
    return (
        <Card className="w-full h-full p-6 glass-card">
            <div className="flex flex-col items-center justify-center text-center h-full">
                <Skeleton className="h-16 w-16 rounded-full mb-4" />
                <Skeleton className="h-8 w-32 mb-2" />
                <Skeleton className="h-4 w-24" />
            </div>
        </Card>
    );
}

export default function SubjectSelection() {
  const { isUnlocked, isInitialized } = useUnlockStore();
  const [justUnlocked, setJustUnlocked] = useState<Subject | null>(null);

  const handleUnlockSuccess = useCallback((subjectId: Subject) => {
    setJustUnlocked(subjectId);
    setTimeout(() => {
        setJustUnlocked(null);
    }, 5000); // Confetti lasts for 5 seconds
  }, []);

  return (
    <>
      {justUnlocked && <Confetti recycle={false} numberOfPieces={400} gravity={0.1} />}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 w-full max-w-5xl">
        {subjects.map((subject) => (
          <div key={subject.id} className="h-80">
            {!isInitialized ? (
              <CardSkeleton />
            ) : (
              <SubjectCard 
                subject={subject} 
                isUnlocked={isUnlocked(subject.id)}
                onUnlockSuccess={handleUnlockSuccess}
              />
            )}
          </div>
        ))}
      </div>
    </>
  );
}
